.hot-label.electric{
  background:#40e0d0;
  color:#fff;
  padding:5px;
  text-transform:uppercase;
  position:absolute;
  top:18px;
  right:-2px;
  z-index:2;
  transform:rotate(45deg)
}

.hot-label.electric:before{
  content:"";
  width:0;
  height:0;
  border-style:solid;
  transform:rotate(45deg);
  border-width:20px 20px 0 20px;
  border-color:#40e0d0 transparent transparent transparent;
  position:absolute;
  right:-27px;
  bottom:-3px
}

.hot-label.electric:after{
  content:"";
  width:0;
  height:0;
  border-style:solid;
  transform:rotate(-45deg);
  border-width:20px 20px 0 20px;
  border-color:#40e0d0 transparent transparent transparent;
  position:absolute;
  left:-27px;
  bottom:-3px
}

.sidebar-open.layout{
